import { BrowserRouter as Router, Routes, Route, Link } from 'react-router-dom';
import loadable from '@loadable/component';

import portoLima from './img/portofolio/lima.png';
import portoAdm from './img/portofolio/lima_adm.png';
import portoNOC from './img/portofolio/noc.png';
import portoPO from './img/portofolio/point_operation.png';
import portoInventory from './img/portofolio/inventory.png';
import portoAyamBakakak from './img/portofolio/ayam_bakakak.jpg';
import portoVendor from './img/portofolio/evaluasi_vendor_lima.png';
import portoQC from './img/portofolio/otto_qc_dept.png';
import portoTicketing from './img/portofolio/ticketing.png';
import portoShibanesia from './img/portofolio/shibanesia.png';
import portoQueenFood from './img/portofolio/queen_food.png';
import portoMieSarmi from './img/portofolio/mie_sarmi.png';

const PortofolioDetail = loadable(() => import('./PortofolioDetail'));

const Portofolio = () => {
    const porto = [
        {'id':1,'url':portoLima,'name':'Website Lintasmediatama'},
        {'id':2,'url':portoAdm,'name':'Adm LIMA'},
        {'id':3,'url':portoNOC,'name':'NOC Lima'},
        {'id':4,'url':portoInventory,'name':'Inventory LIMA'},
        {'id':5,'url':portoAyamBakakak,'name':'Ayam Bakakak Mamah ii'},
        {'id':6,'url':portoPO,'name':'Point Operation'},
        {'id':7,'url':portoVendor,'name':'Evaluasi Vendor LIMA'},
        {'id':8,'url':portoQC,'name':'Aplikasi Pengujian Produk'},
        {'id':9,'url':portoTicketing,'name':'Ticketing LIMA'},
        {'id':10,'url':portoShibanesia,'name':'Shibanesia.id'},
        {'id':11,'url':portoQueenFood,'name':'D Queen Food'},
        {'id':12,'url':portoMieSarmi,'name':'Mie Sarmi'}
    ]

    return(
        <div id="portofolio" className="my-5 py-5">
            <div className="container pb-5 py-5">
                <div className="text-title mb-5">Portofolio</div>
                <Router>
                    <Routes>
                        <Route exact path="/" element={
                            <div className="row">
                                {porto.map((porto) => (
                                    <div key={porto.id} className="col-12 col-md-6 col-lg-4 col-xl-3 mb-3">
                                        <div className="portofolio-container">
                                            <img src={porto.url} alt={porto.name} loading="lazy"></img>
                                            <Link to={'/portofolio/' + porto.id} >
                                                <div className="overlay">
                                                    <div className="title">{porto.name}</div>
                                                </div>
                                            </Link>
                                        </div>
                                    </div>
                                ))}
                            </div>
                        } />
                        <Route path="/portofolio/:id" element={<PortofolioDetail/>} />
                    </Routes>
                </Router>
            </div>
        </div>
    )
}

export default Portofolio;